import React from 'react'
import {Button} from 'antd'
// import FileInput from 'react-file-input'

import './index.scss'
export default class GameUpload extends React.Component{
  constructor(props){
    super(props)
    const value = props.value
    this.state={
      isEdit: value|true
    }
  }

  handleChange(event){
    console.log(event.target.files[0]);
  }

  render(){
    return(
      <div>
      {
        this.state.isEdit
        ?
        <div className='gameUpload'>
          <div className='logo'>
            <img src={require('./ds.png')}/>
          </div>
          <div className="name">
            <p>标题：'123'</p>
            <p>签名：1231233</p>
          </div>
          <div>
            <p>版本代号：211</p>
            <p>版本代号：211</p>
            <p>版本代号：211</p>
          </div>
          <div >
            <label>
             重新上传
              <input 
                type="file"
                accept=".apk,.ipa"
                hidden
                onChange={(e)=>this.handleChange(e)}
                />
            </label>
          </div>
        </div>
        :
        <div className='gameUpload'>
              <label>
              点击上传
              <input 
                type=".apk,.ipa" 
                hidden 
                onChange={(e)=>this.handleChange(e)}
                />
              </label>
        </div>
      }
      </div>
    )
  }
}



