import React from 'react'
import { Form, Select, Input,Modal, Button,Icon, Upload, message ,Steps} from 'antd';
import { uploadimg } from '../../api/login'
import UploadIcon from '../../components/UploadIcon/index'
import CustomBreadcrumb from '../../components/CustomBreadcrumb/index'
import GameUpload from '../../components/gameUpload/index'
import './index.scss'
const { Option } = Select;
const { TextArea } = Input;
class TypeSelect extends React.Component{
    constructor(props) {
      super(props);

      const value = props.value || {};
      this.state = {
        type: value.type || '育儿',
        gtype: value.gtype || '育儿',
      };
    }
    handleTypeChange = (type) => {
      if (!('value' in this.props)) {
        this.setState({ type });
      }
      this.triggerChange({ type });
    }
    handleGtypeChange = (gtype) => {
      if (!('value' in this.props)) {
        this.setState({ gtype });
      }
      this.triggerChange({ gtype });
    }
    render(){
      const { size } = this.props;
      const state = this.state;
      return(
        <span>
        <Select
          type="text"
          size={size}
          value={state.type}
          onChange={this.handleTypeChange}
          style={{ width: '48%', marginRight: '4%' }}
          >
          <Option value="rmb">RMB</Option>
          <Option value="dollar">Dollar</Option>
        </Select>
        <Select
          value={state.gtype}
          size={size}
          style={{ width: '48%' }}
          onChange={this.handleGtypeChange}
        >
          <Option value="rmb">RMB</Option>
          <Option value="dollar">Dollar</Option>
        </Select>
      </span>
      )
    }
}

@Form.create()
class GameInfo extends React.Component{
  checkPrice = (rule, value, callback) => {
    if (value.gtype) {
      callback();
      return;
    }
    callback('请输入分类');
  }
  checkIcon = (rule, value, callback) => {
    console.log(value)
    if (value.length) {
      callback();
      return;
    }
    callback('请输入游戏icon');
  }
  checkImg = (rule, value, callback) => {
    console.log(value)
    if (value.length) {
      callback();
      return;
    }
    callback('请添加游戏图片');
  }
  
  handleSubmit=(e)=>{
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }
  render(){
    const { getFieldDecorator } = this.props.form;
    return(
        <Form onSubmit={this.handleSubmit}>
          <p className="title">
            <span>上传安装包</span>
          </p>
          <GameUpload/>
          <p className="title mt20">
            <span>完善应用信息</span>
          </p>
          <Form.Item
            label="应用名称"
          >
            {getFieldDecorator('name', {
              rules: [{
                required: true, message: '请输入应用名称',
              }],
            })(
              <Input/>
            )}
          </Form.Item>
          <Form.Item
            label="分类"
          >
            {getFieldDecorator('type', {
            initialValue: { type: "鱼儿", gtype: 'rmb' },
            rules: [{ validator: this.checkPrice, required: true }],
          })(<TypeSelect />)}
          </Form.Item>
          <Form.Item
            label="搜索关键词"
          >
            <Input style={{ width: '33%' }}/>
            <Input style={{ width: '33%',marginLeft:'0.5%' }}/>
            <Input style={{ width: '33%',marginLeft:'0.5%' }}/>
            <div>非必填</div>
          </Form.Item>
          <Form.Item
            label="一句话简介"
          >
          {getFieldDecorator('intro', {
            rules: [{  message: '请输入一句话简介',required: true }],
          })(
            <Input/>
          )}
          </Form.Item>
          <Form.Item
            label="应用描述"
          >
          {getFieldDecorator('desc', {
            rules: [{message: '请输入应用描述',required: true}],
          })(
            <TextArea rows={4} />
          )}
          </Form.Item>
          <Form.Item
            label="新版特征"
          >
          {getFieldDecorator('charac', {
             rules: [{message: '请输入新版特征',required: true}],
          })(
            <TextArea rows={4} />
          )}
          </Form.Item>
          <p className="title mt20">
            <span>上传安装包</span>
          </p>
          <Form.Item
            label="应用图标"
          >
          {getFieldDecorator('icon', {
            initialValue: { iconList:[] },
            rules: [{ required: true,validator: this.checkIcon }],
          })(
            <UploadIcon/>
          )}
          </Form.Item>
          <Form.Item
            label="应用截图"
          >
          {getFieldDecorator('icon', {
            initialValue: { iconList:[],max:5 },
            rules: [{ required: true,validator: this.checkIcon }],
          })(
            <UploadIcon/>
          )}
          </Form.Item>
          <Form.Item
          >
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
    )
  }    
}

class UploadGame extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isEdit:false,
      gameFormData:this.props.gameFormData,
      current: 0,
      previewVisible: false,
      previewImage: '',
      fileList: [{
        uid: '-1',
        name: 'xxx.png',
        status: 'done',
        url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
      }],
    }
  }
  onRef=(ref)=>{
    this.uploadCon = ref
  }
  componentWillMount(){
    console.log(this.props.location.query)
    if(this.props.location.query){
      this.setState({
        isEdit:true
      })
    }
    console.log(this.state.isEdit)
  }
  // componentDidMount(){
  //   console.log(this.props.gameFormData)
  //   this.uploadCon.getImgFiles(this.props.gameFormData)
  // }
  handlePhotoChange(val) {
    console.log(val)
  }
  render() {
    const { current ,fileList,previewVisible,previewImage} = this.state
    const Step = Steps.Step;
    const steps = [{
      title: '填写应用信息',
    }, {
      title: '应用审核中',
    }, {
      title: '应用上架',
    }];
    return (
      <div className="fadein handlegame">
        <CustomBreadcrumb arr={['应用']}/>
        <div className="content maxw1000">
          <div className="pa20">
            <Steps current={current}>
              {steps.map(item => <Step key={item.title} title={item.title} />)}
            </Steps>
          </div>
          <br/>
          {/* <div className="addApk">
            <p className="title">
              <span>上传安装包</span>
            </p>
            <GameUpload/>
          </div>
          <div className="mt20">
            <p className="title">
              <span>完善应用信息</span>
            </p>
            <div className="pa20">
              <GameInfo/>
            </div>
          </div> */}
           <GameInfo/>
        </div>
      </div>
    )
  }
}

export default UploadGame



